#equal{
  border-radius: 50em;
  margin: 0.2em;
  width: 100px;
  height: 100px;
  font-family: "Fira Code";
  display: flex;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none
}


hr{
  background-color: #888;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 1024px) {
  .katex {
    font-size: 0.8em !important;
  }

  #equal{
    margin:0;
  }



}


